import React from 'react';
import { useSelector } from 'react-redux';

const SimpleCart = () => {
  const items = useSelector(state => state.cart.items) || [];

  return (
    <div>
      <h2>Cart</h2>
      <ul>
        {items.map(item => (
          <li key={item.id}>
            {item.name} - Quantity: {item.quantity}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default SimpleCart;
